.App {
  text-align: center;
  background-color: #9b8d84;
}

p,
h1,
h2,
h3 {
  font-family: "Geneva";
  padding: 10px;
  font-size: 16px;
}

body {
  height: 100%;
  margin: 0;
}

p5:hover {
  cursor: alias;
}

#menuAreas:hover {
  cursor: pointer;
}

#mri:hover{
  cursor: pointer
}

#menuContainer {
  position: relative;
  text-align: center;
  color: black;
}
#menuTitle {
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#overlay {
  position: fixed;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 1);
  z-index: 1;
  cursor: pointer;
}

#overlayText {
  position: absolute;
  text-align: justify;
  text-justify: inter-word;
  font: 10px "Geneva", sans-serif;
  top: 50%;
  left: 50%;
  font-size: 20px;
  color: white;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}

#backArrow:hover {
  cursor: pointer;
}

#helpButton:hover {
  cursor: help;
}

#start:hover {
  cursor: pointer;
}

#container1 {
  position: relative;
  text-align: center;
  color: white;
}


#draggableArgument:hover {
  cursor: grab;
}

#nondraggableArgument:hover {
  cursor: not-allowed;
}
